import React from 'react';

import axios from 'axios';

import Navbar from './navbar';

import Home from './Home';
import {About} from './AboutInfo';

import {Route, Switch} from 'react-router-dom';
import StateInfo from './StateInfo';

import Precautions from './Precautions';

import News from './news';


class App extends React.Component
{
    state={
        india : [],
        statewise: [],
        tested:[],
        states_tested_data:[],
        districtwise: [],
        zones:[],
        statePopulationData: [],
        default:'India',
       

    }

    

    style ={
        area:{
            display: 'flex',
            background: '#FFF',
            
            borderRadius: '6px',
            fontSize: '12px',
            lineHeight: '20px',
            padding: '10px 16px',
            cursor: 'pointer',
            userSelect: 'none',
            position: 'relative',
            flex: '1',
            alignItems: 'center'
        },

        areaName :
        {
            fontWeight: '600',
            overFlow: 'hidden',
            whiteSpace: 'nowrap',
            textOverflow: 'ellipsis',
            flex: '1'
        }
    }
    constructor()
    {
        super();
        this.getStateTestData = this.getStateTestData.bind(this);
    }


    async componentDidMount(){



        

        if(navigator.onLine)
        {

            var {data}= await axios.get('https://api.covid19india.org/data.json');
          
 
            var {data:{zones}} =await  axios('https://api.covid19india.org/zones.json');
    
  
                
            var result = await axios('https://api.covid19india.org/v2/state_district_wise.json');
 
    
             var {data:{states_tested_data}}=await axios('https://api.covid19india.org/state_test_data.json');
                
    
        }
        else
        {
            data = require('./sample json/data.json');

         let z = require('./sample json/zones.json');
         let results = require('./sample json/state_district_wise.json');
         result = {
             "data": results.map(d=> d)
         }

         zones = z.zones;
         
         
         states_tested_data = require('./sample json/state_test_data.json');
        }







        
                
       
        const {statewise} = data;
   
        const india = statewise.filter(s => s.statecode === 'TT')[0];
        const s = statewise.filter(s=>s.statecode !== 'TT');


        
        //this.setState({zones});
        const tested = data.tested[data.tested.length-1];
      
        
        this.setState({statewise:s,districtwise:result.data,india,tested,zones,states_tested_data});
       
        
        //console.log(this.state.zones);

    }



    getStateTestData(code)
    {
        if(navigator.onLine)
        {
            var d =  this.state.statewise.filter(st => st.statecode=== code);
        var name = d[0] === undefined? '' : d[0].state;

        var result=this.state.states_tested_data.filter(d=> d.state===name); 

        
        let one = {...result[0]};

        let two = {...result[result.length-1]};
        let third = {...two};
        third.populationncp2019projection = one.populationncp2019projection;
      

        
            return third;

        }
        else
        {
            if(this.state.statewise.length !== 0)
            {
                    
             d =  this.state.statewise.filter(st => st.statecode=== code);
              name = d[0] === undefined? '' : d[0].state;

            var {states_tested_data }= this.state.states_tested_data;

             result=states_tested_data.filter(d=> d.state===name);
             
             

            return result[result.length-2];

            
            }
        }  
       
        
    }




    render()
    {
        return (
            
            <React.Fragment>
               
               <div>
                   
                <Navbar />
                
                <div className="content">
                <Switch>
                    <Route path="/about">
                        <About />
                    </Route>
                    <Route path="/precautions">
                        <Precautions  />
                    </Route>
                    <Route path="/news">
                        <News  />
                    </Route>
                    <Route path="/state/:code?" component={(props)=>
                        <StateInfo zones={this.state.zones} getStateTestData={(code)=>this.getStateTestData(code)} statewise={this.state.statewise} districtwise={this.state.districtwise} {...props}/>} />
                    
                    <Route exact path="/">
                        <Home data={this.state}/>
                    </Route>
          
        </Switch>
                
               </div>
               </div>
               
                
            </React.Fragment>
        );
    }
}


export default App;

